import React, { Component } from 'react'
import Slider from 'react-slick'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

export default class ImageSlider extends Component {
  render() {
    var settings = {
      dots: false,
      infinite: false,
      speed: 500,
      slidesToShow: 8,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    }
    return (
      <div>
        <Slider {...settings}>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              1
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              2
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              3
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              4
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              5
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              6
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              7
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              9
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              10
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              11
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              12
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              13
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              14
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              15
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              16
            </div>
          </div>
          <div className="px-1">
            <div
              className="bg-white rounded border p-3"
              style={{ height: '100px' }}
            >
              17
            </div>
          </div>
        </Slider>
      </div>
    )
  }
}
