import { Carousel, Container } from 'react-bootstrap'
import React, { useEffect, useRef } from 'react'

import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import ImageSlider from './ImageSlider'

export default function Banner() {
  const rel = useRef(null)
  useEffect(() => {
    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      60,
      window.innerWidth / window.innerHeight,
      0.1,
      100,
    )
    const renderer = new THREE.WebGL1Renderer({
      canvas: rel.current,
    })
    const controls = new OrbitControls(camera, renderer.domElement)

    renderer.setPixelRatio(window.devicePixelRatio)
    renderer.setSize(window.innerWidth, window.innerHeight)
    camera.position.set(0, 1, 2)

    scene.background = new THREE.Color('black')
    scene.translateY(0.01)

    renderer.render(scene, camera)

    function addStars() {
      const starGeometry = new THREE.SphereGeometry(0.05)
      const starMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff })
      const star = new THREE.Mesh(starGeometry, starMaterial)

      const [x, y, z] = Array(3)
        .fill()
        .map(() => THREE.MathUtils.randFloatSpread(100))
      star.position.set(x, y, z)

      scene.add(star)
    }

    for (let i = 0; i < 500; i++) {
      addStars()
    }

    const loader = new GLTFLoader()
    const light = new THREE.AmbientLight(0xffffff, 1)

    loader.load(
      './images/scene.glb',
      function (glb) {
        glb.scene.scale.set(0.015, 0.015, 0.013)

        scene.add(glb.scene)
      },
      undefined,
      function (error) {
        console.log('glb load error: ', error)
      },
    )

    scene.add(light)

    window.addEventListener('resize', function (e) {
      camera.aspect = window.innerHeight / window.innerHeight
      renderer.setPixelRatio(window.devicePixelRatio)
      renderer.setSize(window.innerHeight, window.innerHeight)
    })

    function animate() {
      requestAnimationFrame(animate)

      controls.update()
      renderer.render(scene, camera)
    }
    animate()
  }, [])

  return (
    <div>
      <Carousel style={{ height: '60vh' }}>
        <Carousel.Item style={{ height: '60vh' }}>
          <canvas
            style={{ height: '100%', width: '100%', display: 'block' }}
            ref={rel}
          ></canvas>
        </Carousel.Item>
      </Carousel>
      <div style={{ background: 'grey' }} className="py-3">
        <Container fluid="md">
          <ImageSlider />
        </Container>
      </div>
    </div>
  )
}
